.info {
  padding: 30px 0;
}
.content__img {
  margin: 0 auto;
  width: auto;
  height: auto;
  max-height: 300px;
  border-radius: 15px;
}
.logo__img_img {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin-right: 20px;
}
.info__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  margin-top: 10px;
  border-radius: 15px;
  background-color: rgb(196, 196, 196);
}
.logo__descr {
  padding-bottom: 20px;
}
.name__inner{
  width: 100%;

}
.contact{
  padding-left: 10px;
}
.button_ed,
.button_sv{
  padding: 15px 5px;
  border:none;
  cursor: pointer;
  background-color: rgb(138, 253, 234);
  width: 100%;
  margin: 10px 0;
  border-radius:15px;
  color: rgb(48, 46, 46);
  max-width: 200px;
  height: auto;
  text-transform: uppercase;
  font-size: 13px;
  transition: 0.3s;
  outline: none;
}
.button_ed:hover {
  transition: 0.3s;
  background-color: rgb(213, 228, 239);
}
.button_ed{

}
.button_sv{

}
.edit_item{
  padding-bottom: 5px;
  padding-top: 5px;
}
.edit_item strong {
  font-size: 19px;
}
.edit_item span{
  font-size: 17px;
  color: #5700f8;
}

